.HomeContainer {
    height: 90vh;
    width: 100%;
    /* background-color: red; */
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}

.LogoContainer {
    margin: 3% 0;
    width: auto;
    max-height: 30vh;
    background-color: black;
    color: white;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar {
    position: sticky;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    align-items: center;
    text-align: center;
    height: 55px;
    margin: 0 auto;
    box-shadow: 0 0 5px black;
    z-index: 10;
}

.navbarContainer {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #000000e0;
    height: 100%;
    align-items: center;
}

.navbarBox {
    width: 95%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-size: 18px;
    font-weight: 900;
    font-style: italic;
    color: white;
}

.redColor {
    color: red !important;
}

.inputFeild {
    font-size: 20px;
    outline: none;
    border: none;
    padding: 3px 6px;
    border-radius: 5px;
    width: 100%;
}

.searchContainer {
    position: relative;
    width: 400px;
}

.searchIcon {
    position: absolute;
    width: 20px;
    height: 100%;
    right: 2%;
    color: black;
    top: 25%;
    bottom: auto;
    display: flex;
    justify-content: center;
    cursor: pointer;
}

.img {
    width: 100%;
    height: 100%;
    border-radius: 20px;

}

.intro {
    height: 50vh;
    margin: 1% 0;
    width: 95%;
    border-radius: 20px;
    display: flex;
    /* justify-content: space-evenly; */
    color: white;
    flex-direction: row;
    flex-flow: row wrap;
    
}
.card{
    display: flex;
    background-color: red;
    margin: 1%;
    flex: 1 ;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    text-align: center;
    border-radius: 20px;
    padding: 1%;
}
.green{
    background-color: rgb(0, 131, 143);
}
.blue{
    background-color: rgb(21, 101, 192);
}
.purple{
    background-color: rgb(69, 39, 160);
}
.darkblue{
    background-color: rgb(40, 53, 147);
}
.none{
    display: none !important;
}
@media (max-width: 425px) {
    .LogoContainer{
        max-height: 24vh;
    }
    .searchContainer{
        width: 200px;
    }
}